.select-program-owner {
  padding: 0;
  border: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #222222;
  margin: 43px 0 10px;
}

.select-program-owner-line {
  border-top: 3px solid #68b738;
  margin-top: 26px;
}

.select-program-owner__title--elevated {
  margin-top: -25px;
}

.select-program-owner__wrapper {

  box-sizing: border-box;
  width: 320px;
  padding: 0;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  list-style: none;
  border: 0;
}

.select-program-owner__title {
  text-transform: none;
  font-size: 24px;
  line-height: 24px;
  color: #111111;
  margin-bottom: 35px;
  background: #ffffff;
  width: auto;
  max-width: 222px;
  padding-left: 20px;
}

.select-program-owner__field {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  margin-bottom: 14px;
  padding-left: 20px;
  padding-right: 20px;
}

.select-program-owner__field-control {
  flex-grow: 1;
  width: 50%;
  height: 45px;
  margin-left: 15px;
  padding: 12px 13px;
  border: 1px solid #e6e6e6;
  text-transform: uppercase;
  font-family: inherit;
}

@media (min-width: 768px) {
  .select-program-owner {
    margin-top: 115px;
  }

  .select-program-owner__wrapper {
    padding-right: 20px;
    font-size: 20px;
    line-height: 20px;
    margin-bottom: 41px;
    width: 768px;
  }

  .select-program-owner__title {
    font-size: 36px;
    line-height: 36px;
    max-width: 594px;
    font-weight: 400;
    padding-left: 40px;
    padding-right: 40px;
    margin-left: 27px;
    margin-bottom: 77px;
  }

  .select-program-owner__field {
    padding-left: 72px;
    padding-right: 40px;
    margin-bottom: 20px;
  }

  .select-program-owner__field-control {
    height: 52px;
    padding: 14px 20px 13px;
    font-size: 20px;
    line-height: 30px;
  }
}
